<template>
    <div id="modelsPage">
        <h1>{{ msg }}</h1>
        <h3>Models</h3>
        <p>List of signed models at ModelsManagement.</p>
        <table class="table">
            <thead>
                <tr>
                    <th>First Name</th>
                    <th>Last Name</th>
                    <th>Email</th>
                </tr>
            </thead>
            <tbody>
              
            </tbody>
        </table>

        <button type="button" @click="addModel()">Add model</button>
        <button type="button" @click="deleteModel()">Delete model</button>
        
    </div>
</template>

<script>
    //let jwt = localStorage.getItem("token");
    //let jwtData = jwt.split('.')[1]
    //let decodedJwtJSONData = window.atob(jwtData)
    //let decodedJwtData = JSON.parse(decodedJwtJSONData)

    //let role = decodedJwtData["http://schemas.microsoft.com/ws/2008/06/identity/claims/role"];

    //if (role == "Manager") {
        
    //}

    export default {
        name: 'Home',
        props: {
            msg: String
        },

        methods: {
            // Tilf�jer model, n�r man har trykket p� "Add-model"-knappen
            async addModel() {
                fetch('http://localhost:44368/api/Models', {
                    method: 'POST',
                    credentials: 'include',
                    headers: {
                        'Authorization': 'Bearer ' + localStorage.getItem("token"),
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify( //stringfy = konverterer alle elementer i json-objektet tils stringe
                        {
                            firstName: this.firstName,
                            lastName: this.lastName,
                            email: this.email,
                            phoneNo: this.phoneNo,
                            addresLine1: this.addresLine1,
                            addresLine2: this.addresLine2,
                            zip: this.zip,
                            city: this.city,
                            country: this.country,
                            birthDate: this.birthDate,
                            nationality: this.nationality,
                            height: this.height,
                            shoeSize: this.shoeSize,
                            hairColor: this.hairColor,
                            eyeColor: this.eyeColor,
                            comment: this.comment,
                            password: this.password
                        }),
                }).then(resonse => {
                    var items = resonse;
                    items.msg // mangler!
                }).catch(error => alert({
                    isLoading: false,
                    message: 'Something bad happened ' + error
                }));
            },

            async deleteModel() {
                // mangler at specificere id p� modellen som skal slettes!
                fetch('http://localhost:44368/api/models/${id}'), {
                    method: 'DELETE'
                }  
            },
        }
    }
</script>
<style></style>
